.wadi_loader {
  display: block;
  color: #000000;
  font-size: 20px;
  margin: 100px auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load4 1.3s infinite linear;
  animation: load4 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}

@-webkit-keyframes load4 {
  0%, 100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}
@keyframes load4 {
  0%, 100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}
.wadi_table_container {
  width: 100%;
  min-width: 100%;
}
.wadi_table_container .wadi_table {
  width: 100%;
  min-width: 100%;
}
.wadi_table_container .wadi_table thead tr {
  background-color: #2F45BC;
}
.wadi_table_container .wadi_table thead tr th {
  padding: 1rem;
}
.wadi_table_container .wadi_table thead tr th .wadi_table_cell_text .wadi_header_image_cell_container img {
  width: 60px;
  height: auto;
  vertical-align: middle;
  display: inline-block;
}
.wadi_table_container .wadi_table thead tr th .wadi_table_cell_text .wadi_heading_icon_align {
  display: inline-block;
  vertical-align: middle;
}
.wadi_table_container .wadi_table tbody tr.wadi_table_row {
  background-color: #FFE7AE;
}
.wadi_table_container .wadi_table tbody tr td .wadi_image_cell_container img {
  width: 60px;
  height: auto;
  vertical-align: middle;
  display: inline-block;
}
.wadi_table_container .wadi_table tbody tr td .wadi_body_align_icon {
  display: inline-block;
  vertical-align: middle;
}
.wadi_table_container .dataTables_filter {
  display: block;
  float: right;
}
.wadi_table_container .dataTables_length {
  display: block;
  float: left;
}
.wadi_table_container .dt-buttons {
  display: inline-block;
  margin: 0 0 20px 20px;
}
.wadi_table_container .dataTables_paginate {
  display: flex;
  overflow: hidden;
  perspective: 1px;
}
.wadi_table_container .dataTables_paginate span {
  display: flex;
}
.wadi_table_container .dataTables_paginate .paginate_button {
  display: block;
  cursor: pointer;
}
.wadi_table_container table.dataTable thead th {
  position: relative;
  background-image: none !important;
}
.wadi_table_container table.dataTable thead th.sorting:after,
.wadi_table_container table.dataTable thead th.sorting_asc:after,
.wadi_table_container table.dataTable thead th.sorting_desc:after {
  position: absolute;
  top: 50%;
  right: 8px;
  display: block;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  transform: translate(-50%, -50%);
}
.wadi_table_container table.dataTable thead th.sorting:after {
  content: "\f0dc";
}
.wadi_table_container table.dataTable thead th.sorting_asc:after {
  content: "\f0de";
}
.wadi_table_container table.dataTable thead th.sorting_desc:after {
  content: "\f0dd";
}
.wadi_table_container .wadi_table_pagination {
  float: left;
  margin-top: 1rem;
}
.wadi_table_container .wadi_table_pagination .paginate_button, .wadi_table_container .wadi_table_pagination .ellipsis {
  background: #2F45BC;
  color: #fff;
  border: 1px solid #7585dc;
  border-radius: 0;
  padding: 0.6rem;
}
.wadi_table_container .wadi_table_pagination .paginate_button:hover, .wadi_table_container .wadi_table_pagination .ellipsis:hover {
  background: #7585dc;
  color: #fff;
  border: 1px solid #1b276a;
  border-radius: 0;
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  .wadi_table_container {
    overflow-x: scroll;
  }
  .wadi_table_container .dataTables_filter {
    display: block;
    width: 100%;
    padding: 1rem;
  }
  .wadi_table_container .dataTables_filter label {
    display: block;
    width: 100%;
  }
  .wadi_table_container .dataTables_filter input {
    width: 100%;
  }
  .wadi_table_container .dataTables_length {
    display: block;
    width: 100%;
    padding: 1rem;
  }
  .wadi_table_container .dataTables_length label {
    display: block;
    width: 100%;
  }
  .wadi_table_container .dataTables_length select {
    width: 100%;
  }
  .wadi_table_container .dt-buttons {
    display: inline-block;
    width: 100%;
    padding: 1rem;
    margin: 0;
  }
}
.wadi_table_sticky_header_yes .wadi_table_container thead {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}

@media screen and (max-width: 600px) {
  .admin-bar .wadi_table_sticky_header_yes .wadi_table_container thead {
    top: 0;
  }
}
@media screen and (min-width: 601px) and (max-width: 782px) {
  .admin-bar .wadi_table_sticky_header_yes .wadi_table_container thead {
    top: 45px;
  }
}
@media (min-width: 783px) {
  .admin-bar .wadi_table_sticky_header_yes .wadi_table_container thead {
    top: 30px;
  }
}
.wadi_table_sticky_header_yes .wadi_table_container {
  overflow: unset;
}